:root{
  font-family: calibri;
}
body{
  background-color: rgb(58,56,67);
  background: linear-gradient(131deg, rgb(58,56,67) 50%, transparent 50%), linear-gradient(131deg, rgb(58,56,67) 50%, rgb(237,237,237) 50%);
  display: flex;
  justify-content: center;
}  
.body{
  background-color: white;
  border-radius: 2px;
  margin: 20px 20px 40px 20px;
  padding: 10px;
  min-width: 800px;
  max-width: 800px;
  min-height: 950px;
  box-shadow: 5px 5px 5px ;
}
#body_border{
  border: solid black 1px;
  margin: 10px 10px 10px 10px;
}
#body_border2{
  border: solid black 1px;
  margin: 1px 1px 1px 1px;
  padding: 30px 30px 30px 30px 
}
.main{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;  
}
#title-container{
  text-align: center;
  font-size: 35px;
  align-self: flex-start;
}
#about-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#about-profile{
  flex-grow: 1;
}
#about-links{
  flex-direction: column;
  align-items: center;
  padding: 0px 20px 0px 20px;
  width: 97vw;
}
#main-container{
  display: flex;
}
.edex{
  text-align: right;
  font-family: fira code, monospace;
  min-width: 237px;
}
.education{
  flex-direction: column;
  padding: 0px 20px 10px 10px;
}
.expertise{
  flex-direction: column;
  padding: 0px 20px 10px 10px;
}
.experience{
  padding: 0px 10px 10px 0px;
  flex-direction: column;
  text-align: left;
}
h1{
  margin-top: 0px;
  font-family: fira code;
  margin: 0px;
}
h2{
  margin: 0px 0px 10px 0px;
}
h4{
  margin: 10px 0px 10px 0px;
}
.font{
  font-family: fira code, monospace;
  margin-left: 20px;
}
hr{
  height: 2px;
  border: none;
  border-radius: 3px;
  background-color: rgb(58,56,67)
}
p{
  margin: 0px;
}
img{
  float: left;
  margin-right: 10px;
}
#pen{
  align-self: center;
}
#ul {
  list-style-type: none; 
  padding: 0; 
  margin: 0;
  }